.roster {
  background: #ffffff;
  padding: 1.5rem 1rem;
  border-radius: 5px;
}

.roster .ant-form-item-label {
  text-align: left;
  width: 110px;
}

.roster .bg-dark {
  float: right;
  width: 8rem;
  height: 2.5rem;
}

.roster__dropOption {
  display: flex;
  justify-content: space-between;
}

.roster__dropOption span:last-child {
  font-size: 0.8rem;
  color: gray;
}

.rosterClosing {
  font-size: 0.8rem;
}

.rosterClosing > p {
  line-height: 2rem;
}

.rosterClosing > p > span {
  color: var(--primary-color);
  font-size: 1rem;
  margin-left: 2px;
}

@media (max-width: 575px) {
  .roster .bg-dark {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .roster__dropOption {
    flex-direction: column;
  }
}
