.viewEmployeeDetails {
  background-color: #ffffff;
  padding: 1rem 1.5rem;
  border-radius: 5px;
}

.viewEmployeeDetails > p {
  font-size: 0.8rem;
  padding-bottom: 0.4rem;
}

.viewEmployeeDetails > p > span {
  font-size: 1rem;
  color: var(--primary-color);
  margin-left: 2px;
}

.viewEmployeeDetails .viewEmployeeDetails__table {
  margin-top: 2rem;
}

.viewEmployeeDetails
  .viewEmployeeDetails__table
  .viewEmployeeDetails__subTasks {
  list-style: none;
  margin-bottom: 0;
}

.viewEmployeeDetails
  .viewEmployeeDetails__table
  .viewEmployeeDetails__subTasks
  li:not(:last-child) {
  margin-bottom: 0.4rem;
}

@media screen and (max-width: 520px) {
  .viewEmployeeDetails
    .viewEmployeeDetails__table
    .viewEmployeeDetails__subTasks
    li:not(:last-child) {
    margin-bottom: 0.6rem;
  }
}
