.newSchedule .newSchedule__btnHide {
  display: none;
}

/* Label */
.newSchedule .newSchedule__form .ant-form-item-label {
  text-align: left;
  width: 110px;
}
/* Label */

.newSchedule .newSchedule__form .newSchedule__formValues {
  font-size: 0.8rem;
}

.newSchedule .newSchedule__form .newSchedule__formValues > p {
  line-height: 2rem;
}

.newSchedule .newSchedule__form .newSchedule__formValues > p > span {
  color: var(--primary-color);
  font-size: 1rem;
  margin-left: 2px;
}

.newSchedule .newSchedule__form .newSchedule__formValues .newSchedule__table {
  margin-top: 10px;
}

.newSchedule
  .newSchedule__form
  .newSchedule__formValues
  .newSchedule__table
  .ant-table-tbody
  > tr
  > td,
.newSchedule
  .newSchedule__form
  .newSchedule__formValues
  .newSchedule__table
  .ant-table-thead
  > tr
  > th {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 0.9rem;
}

.newSchedule__dropOption {
  display: flex;
  justify-content: space-between;
}

.newSchedule__dropOption span:last-child {
  font-size: 0.8rem;
  color: gray;
}

@media screen and (max-width: 575px) {
  /* modal */
  .newSchedule .ant-modal-body {
    max-height: 300px;
    overflow-x: auto;
  }
  /* modal */
}

@media screen and (max-width: 480px) {
  .newSchedule__dropOption {
    flex-direction: column;
  }
}
