.newJob {
  background-color: #ffffff;
  padding: 1.5rem 1rem;
  border-radius: 5px;
}

/* Label */
.newJob .ant-form-item-label {
  text-align: left;
  width: 110px;
}
/* Label */

.newJob .newJob__createBtn {
  text-align: end;
  margin-top: 1rem;
}

.newJob .newJob__createBtn > button {
  width: 8rem;
  height: 2.5rem;
}

.newJobClosing {
  font-size: 0.8rem;
}

.newJobClosing > p {
  line-height: 2rem;
}

.newJobClosing > p > span {
  color: var(--primary-color);
  font-size: 1rem;
  margin-left: 2px;
}

.newJob__dropOption {
  display: flex;
  justify-content: space-between;
}

.newJob__dropOption span:last-child {
  font-size: 0.8rem;
  color: gray;
}

.btnHide {
  display: none;
}

@media screen and (max-width: 575px) {
  /* DatePicker */
  .newJob .ant-picker-large {
    width: 100%;
  }
  /* DatePicker */

  .newJob .newJob__createBtn > button {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .newJob__dropOption {
    flex-direction: column;
  }
}
