.assignStaff {
  background-color: #ffffff;
  padding: 1.5rem 1rem;
  border-radius: 5px;
}

.assignStaff .assignStaff__assignBtn {
  text-align: end;
}

.assignStaff .assignStaff__assignBtn > button {
  padding: 0 2rem;
  height: 2.5rem;
}

.assignStaff__modal {
  font-size: 0.8rem;
}

.assignStaff__modal p span {
  color: var(--primary-color);
  font-size: 1rem;
  margin-left: 2px;
}

@media screen and (max-width: 420px) {
  .assignStaff .assignStaff__assignBtn > button {
    width: 100%;
    margin-top: 1rem;
  }
}
