.add_user_content {
    display: flex;
    flex-direction: column;
}

.add_user_content .link{
    margin-bottom: 20px;
}


.dashItem {
    background: #ffffff;
    border-radius: 5px;
    padding: 1.6rem;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
    height: 100%;
  }
  
  .dashItem:hover {
    background-color: var(--primary-color);
    color: #ffffff;
    transition: 0.2s all;
  }
  
  @media screen and (max-width: 290px) {
    .dashItem {
      padding: 1rem;
    }
  }